<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <div class="card-body">
    <section class="mb-4">
      <!-- type -->
      <div class="mb-4">
        <h5>TYPE：</h5>
        <template v-for="(item, index) in types" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-type"
            :id="`type${index}`"
            autocomplete="off"
            :value="item"
            v-model="option.type"
            @click="option.quantity = 1;"
          />
          <label class="btn border-0 position-relative me-4" :for="`type${index}`"
            >{{ item }}
            <CircleTwo :class="{ 'd-none': option.type !== item }" />
          </label>
        </template>
      </div>
      <!-- flavor -->
      <div class="mb-4">
        <h5>FLAVOR：</h5>
        <template v-for="(item, index) in productList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`flavor${index}`"
            autocomplete="off"
            :value="item.title"
            v-model="option.flavor"
            @click="tempProduct = item; option.quantity = 1; option.price = item.price"
          />
          <label
            class="btn border-0 position-relative me-4"
            v-if="item.category === option.type"
            :for="`flavor${index}`"
            >{{ item.title }}
            <CircleThree :class="{ 'd-none': item.title !== option.flavor }" />
          </label>
        </template>
      </div>
      <!-- price -->
      <div class="mb-4">
        <h5>PRICE：</h5>
        <p class="ms-3">
          $ <span class="text-secondary">{{ tempProduct.price }}</span> / EACH
        </p>
      </div>
      <!-- 數量＆＄ -->
      <div class="mb-4">
        <h5 class="text-uppercase">quantity：</h5>
        <nav aria-label="..." class="me-4">
          <ul class="pagination">
            <li class="page-item">
              <a
                class="page-link rounded-0"
                href="#"
                @click.prevent="option.quantity--"
                :class="{ disabled: option.quantity <= 1 }"
                >-</a
              >
            </li>
            <li class="page-item">
              <div class="page-link">{{ option.quantity }}</div>
            </li>
            <li class="page-item">
              <a
                class="page-link rounded-0"
                href="#"
                @click.prevent="option.quantity++"
                :class="{ disabled: option.quantity >= 10 }"
                ><i class="bi bi-plus-lg"></i
              ></a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 總金額 -->
      <div class="">
        <h5 class="text-uppercase">total：</h5>
        <div class="fst-italic ms-3 h4 text-secondary luluText">
          ${{ this.$toCurrency(tempProduct.price * option.quantity) }}
        </div>
      </div>
    </section>
    <button
      class="btn btn-outline-primary rounded-0 mb-3"
      @click="this.$emitter.emit('addCart',{ ...option })"
    >
      <i class="bi bi-caret-right-fill"></i>Add to Cart
    </button>
  </div>
</template>

<style lang="scss" scoped>
.circle-2 {
  width: 6rem;
}
.circle-3 {
  width: 7rem;
}
</style>

<script>
import productJson from '@/assets/jsondata/products.json';
import CircleThree from '../models/circle/CircleThree.vue';
import CircleTwo from '../models/circle/CircleTwo.vue';

export default {
  data() {
    return {
      tempProduct: {},
      productList: [],
      flavorList: [],
      types: ['dish', 'cake', 'cookie'],
      option: {
        type: 'dish',
        flavor: '',
        select: '',
        price: 0,
        quantity: 1,
      },
    };
  },
  components: {
    CircleThree,
    CircleTwo,
  },
  watch: {
    // eslint-disable-next-line object-shorthand
    'option.type'() {
      this.selectFlavor();
    },
    tempProduct() {
      this.$emitter.emit('infoData', this.tempProduct);
    },
  },
  methods: {
    selectFlavor() {
      this.flavorList = [];
      this.productList.forEach((item) => {
        if (this.option.type === item.category) {
          this.flavorList.push(item);
        }
      });
      this.option.flavor = this.flavorList[0].title;
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.flavorList[0];
      this.option.price = this.flavorList[0].price;
    },
  },
  mounted() {
    this.productList = productJson;
    this.selectFlavor();
  },
};
</script>
