<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="card-body p-0">
    <table class="table tableText tableSm table-bordered border-primary m-0">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">PRODUCT</th>
          <th scope="col">PRICE</th>
          <th scope="col">Qty</th>
          <th scope="col">SUB</th>
        </tr>
      </thead>
      <tbody class="table-group-divider luluText">
        <template v-for="(item, index) in cartList" :key="index">
          <tr>
            <td>{{ index + 1 }}</td>
            <td>
              <div class="d-flex align-items-center text-sm-nowrap">
                <i
                  class="bi bi-circle-fill me-1"
                  :class="{
                    'text-danger': item.type === 'dish',
                    'text-info': item.type === 'cake',
                    'text-secondary': item.type === 'cookie',
                  }"
                  style="font-size: 8px"
                ></i
                >{{ item.flavor }}
              </div>
            </td>
            <td>${{ item.price }}</td>
            <td class="text-center">
              <a href="#" @click.prevent="cartStatus(index, -1)" class="me-1 text-decoration-none"
                >-</a
              ><span>{{ item.quantity }}</span
              ><a href="#" class="ms-1 text-decoration-none" @click.prevent="cartStatus(index, 1)"
                >+</a
              >
            </td>
            <td>${{ this.$toCurrency(item.quantity * item.price) }}</td>
          </tr>
        </template>
        <tr>
          <td colspan="2">
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-danger mb-0"></i>
              <h6>dish</h6>
            </div>
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-info mb-0"></i>
              <h6>cake</h6>
            </div>
            <div class="d-flex align-items-center">
              <i class="bi bi-dot h3 text-secondary mb-0"></i>
              <h6>cookie</h6>
            </div>
          </td>
          <td colspan="2" class="align-middle">
            total: ${{ this.$toCurrency(total) }}
          </td>
          <td colspan="1" class="text-sm-end text-center align-middle">
            <i class="bi bi-caret-right-fill"></i>CHECKOUT
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<style lang="scss" scoped>
.dashBottom {
  margin-bottom: -30px;
}
@media (min-width: 1024px) {
  .tableText {
    font-size: 24px;
  }
}
@media (max-width: 488px) {
  .dashBottom {
    margin-bottom: -15px;
  }
}
@media (max-width: 377px) {
  .tableSm > :not(caption) > * > * {
    padding: 0.25rem 0.25rem;
  }
}
</style>

<script>
export default {
  data() {
    return {
      cartList: [],
      total: 0,
      finalTotal: 0,
    };
  },
  methods: {
    cartStatus(index, num) {
      this.cartList[index].quantity += num;
      if (this.cartList[index].quantity === 0) {
        this.cartList.splice(index, 1);
      }
      this.populateCarts();
    },
    populateCarts() {
      //   計算總金額
      this.total = 0;
      this.cartList.forEach((item) => {
        this.total += item.price * item.quantity;
      });
    },
  },
  mounted() {
    this.$emitter.on('addCart', (e) => {
      const isExit = this.cartList.findIndex((item) => item.flavor === e.flavor);
      if (isExit >= 0) {
        this.cartList[isExit].quantity += e.quantity;
      } else {
        this.cartList.push(e);
      }
      this.populateCarts();
    });
  },
};
</script>
